@import url(https://fonts.googleapis.com/css?family=Orbitron|Roboto|Text+Me+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  width: 100vw;
  color: #ff6347;
  padding: 20px 0;
}

.header h1 {
  text-align: center;
  font-size: 25px;
}

.quote {
    flex: 1 1 auto;
    width: 25%;
    height: 435px;
    color: #2c3d55;
    box-shadow: 0 0 1px 1px rgb(160, 227, 219);
    border-radius: 15px;
    text-align: center;
    margin-left: 10px;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    font-family: "Text Me One", sans-serif;
}

.quote p {
    padding: 0 20px;
    font-size: 23px;
}

.quote p:before,
.quote p:after {
    content: "\201C";
    color: #ff6347;
    padding: 0 10px;
    font-size: 50px;
    font-weight: bold;
    font-family: auto;
    display: block;
    text-align: left;
    font-family: Arial, Helvetica, sans-serif;
}

.quote p:after {
    content: "\201D";
    text-align: right;
}

.quote-author {
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    color: #ff6347;
}

.quote-author:before {
    content: "-- ";
}

@media (min-width: 320px) and (max-width: 480px) {
    .quote {
        width: 100%;
        height: auto;
        margin: 0 10px;
        padding: 0 5px;
    }
    .quote p {
        padding: 0;
        font-size: 18px;
        margin: 0;
    }
    .quote p:before,
    .quote p:after {
        font-size: 25px;
        padding: 0 5px;
        margin: 10px 0 0 0;
    }
}
.btn {
    padding: 10px 0;
    min-width: 90px;
    margin: 10px;
    outline: none;
    border-radius: 8px;
    border: 1px solid transparent;
    cursor: pointer;
    -webkit-transition: all 2ms ease;
    transition: all 2ms ease;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    background-color: #e5e5e5;
}

.btn-start {
    box-shadow: 0 0 1px 1px #6dc9dd;
}

.btn-start:hover {
    box-shadow: 0 0 2px 2px #6dc9dd;
}

.btn-start:active {
    background: -webkit-linear-gradient(#f4f3ea, #6dc9dd);
    background: linear-gradient(#f4f3ea, #6dc9dd);
}

.btn-stop {
    box-shadow: 0 0 1px 1px #d36582;
}

.btn-stop:hover {
    box-shadow: 0 0 2px 2px #d35682;
}

.btn-stop:active {
    background: -webkit-linear-gradient(#f4f3ea, #d36582);
    background: linear-gradient(#f4f3ea, #d36582);
}

.btn-reset {
    box-shadow: 0 0 1px 1px #b8cdf8;
}

.btn-reset:hover {
    box-shadow: 0 0 2px 2px #b8cdf8;
}

.btn-reset:active {
    background: -webkit-linear-gradient(#f4f3ea, #becdf8);
    background: linear-gradient(#f4f3ea, #becdf8);
}
.timer-container {
    flex: 2 1 auto;
    align-self: flex-start;
    border-radius: 15px;
    box-shadow: 0 0 1px 2px rgba(102, 51, 153, 0.4);
    padding: 40px 0;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    margin: 0 10px;
}

.timer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.clock {
    height: 200px;
    width: 200px;
    box-shadow: 0 0 0 10px rgba(255, 99, 71, 0.9), 0px 0px 0 6px inset rgba(255, 99, 71, 0.5);
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 45px;
    font-weight: bold;
    margin: 15px;
}

@media (min-width: 320px) and (max-width: 480px) {
    .clock {
        height: 180px;
        width: 180px;
        margin: 20px 40px 30px 40px;
        font-size: 40px;
        flex-flow: column wrap;
    }
    .timer-container {
        box-shadow: none;
        padding: 5px 0;
    }
    .timer {
        flex-flow: column wrap;
    }
}
.settings {
  padding: 20px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  margin: 0 auto;
  width: 90%;
}

.settings h2 {
  margin-left: 15px;
  font-size: 18px;
  width: 100%;
}

.timer-settings {
  flex: 1 1 auto;
}

.other-settings {
  flex: 1 1 auto;
}

.settings label {
  display: inline-block;
  margin: 10px;
}

.settings input[type="number"] {
  width: 40px;
  padding: 5px;
  margin-right: 15px;
  box-shadow: 0 0 1px 1px #2c3d55;
  border-radius: 3px;
  border: 0;
  font-size: 18px;
}

.settings input[type="number"]:focus {
  box-shadow: 0 0 2px 2px #2c3d55;
  outline: 0;
}

.settings input[type="checkbox"] {
  display: none;
}

.settings span {
  width: 16px;
  height: 16px;
  margin-right: 15px;
  box-shadow: 0 0 1px 1px #2c3d55;
  border-radius: 3px;
  cursor: pointer;
  position: relative;
  background-color: #ffffff;
  display: inline-block;
  vertical-align: middle;
}

.settings span:hover {
  box-shadow: 0 0 2px 2px #2c3d55;
}

.settings input[type="checkbox"]:checked + span::before {
  content: "";
  position: absolute;
  border-left: 2px solid #2c3d55;
  border-bottom: 2px solid #2c3d55;
  width: 10px;
  height: 3px;
  top: 4px;
  left: 2px;
  -webkit-transform: rotate(80deg);
  transform: rotate(315deg);
}

@media (min-width: 320px) and (max-width: 480px) {
  .settings {
    flex-flow: column wrap;
    align-items: center;
  }
  .settings h2 {
    margin: 8px;
    text-align: left;
  }
}

.todo-container {
    align-self: flex-start;
    min-height: 425px;
    margin-right: 10px;
    flex: 1 1 auto;
    width: 28%;
    box-shadow: 0 0 1px 1px rgb(255, 255, 0);
    border-radius: 15px;
}

.todo-title {
    text-align: center;
    color: #a0099f;
    font-size: 25px;
}

.todo-form {
    border: 1px solid #a0099f;
    display: flex;
    margin: 0 auto;
    border-radius: 15px;
    width: 80%;
}

.todo-input {
    padding: 10px;
    outline: none;
    border: 0;
    color: #2c3d55;
    font-weight: bold;
    font-size: 16px;
    flex: 2 1 auto;
    border-radius: 15px;
    background: #ffffff;
}

.btn-add-todo {
    outline: none;
    border: 0;
    border-top-right-radius: 14px;
    border-bottom-right-radius: 14px;
    width: 60px;
    background: #a0099f;
    color: #ffffff;
    cursor: pointer;
}

.btn-add-todo:active {
    background: -webkit-linear-gradient(#ffffff, #d377c8);
    background: linear-gradient(#ffffff, #d377c8);
}

.todo-filter {
    text-align: center;
}

.todo-filter input[type="button"] {
    border: 0;
    outline: 0;
    margin: 3px;
    padding: 3px 10px;
    font-size: 15px;
    cursor: pointer;
}

.todo-items li {
    list-style-type: none;
    padding: 8px;
    font-size: 16px;
}

.todo-items input[type="checkbox"] {
    display: none;
}

.todo-items span {
    box-shadow: 0 0 1px 1px #a0099f;
    border-radius: 3px;
    position: relative;
    margin-right: 10px;
    height: 16px;
    width: 16px;
    display: inline-block;
    vertical-align: middle;
}

.todo-items input[type="checkbox"]:checked+span::before {
    content: "";
    position: absolute;
    border-left: 2px solid #a0099f;
    border-bottom: 2px solid #a0099f;
    width: 10px;
    height: 3px;
    top: 4px;
    left: 2px;
    -webkit-transform: rotate(80deg);
    transform: rotate(315deg);
}

.no-tasks {
    text-align: center;
    padding: 50px 0;
}

@media (min-width: 320px) and (max-width: 480px) {
    .todo-container {
        box-shadow: none;
    }
    .todo-form {
        width: 90vw;
    }
    .todo-items {
        padding-left: 10px;
        margin: 0 auto;
    }
}
body {
    padding: 0;
    background: #f4f3ea;
    color: #41463d;
    font-family: "Roboto", sans-serif;
}

.container {
    display: flex;
    flex-flow: row wrap;
}

@media (min-width: 320px) and (max-width: 480px) {
    body {
        background-color: #ffffff;
    }
}
